@import '../../util/variables';

.Projects {
  height: 91vh;
  margin-top: 2%;
  width: 100%;
  margin-left: 2%;
}

.projects-grid {
  height: 100%;
  display: grid;
  grid-template-columns: 20% 25% 25% 25%;
  grid-template-rows: 40% 40%;
}

.projects-other {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 80%;
}

.iced-div {
  grid-column: 2;
  grid-column-end: 2;
  grid-row: 1;
  grid-row-end: 1;
  margin: auto;
  padding: 2%;
}

.cycle-div {
  grid-column: 3;
  grid-row: 1;
  margin: auto;
  padding: 2%;
}

.cinema-div {
  grid-column: 4;
  grid-row: 1;
  margin: auto;
  padding: 2%;
}

.idea-div {
  grid-column: 2;
  grid-row: 2;
  margin: auto;
  padding: 2%;
}

.cowboy-div {
  grid-column: 3;
  grid-row: 2;
  margin: auto;
  padding: 2%;
}

.swapi-div {
  grid-column: 4;
  grid-row: 2;
  margin: auto;
  padding: 2%;
}


.gutenreader-div {
  display: inline-block;
  overflow: hidden;
  position: relative;
  grid-column-start:1;
  grid-row: 1 / span 2;
  margin: auto;
  padding: 2%;
}

.gutenpics {
  width: 98%;
}

.projects-div {
  opacity: .3;
  &:hover {
    opacity: 1
  }
}

.projects-div-footer {
  display: flex;
  justify-content: space-between;
  bottom:0;
  width: 100%;
  box-shadow: $box-shadow1;
  height: 4vh;
  align-items: center;
}

.projects-div-footer > a {
  font-weight: 600;
  font-size: 1.1em;
  color: $lighter-grey;
  &:hover {
    color: $secondary-blue;
  }
  &:focus {
    transform: scale(1.1)
  }
}

.guten-info-svg,
.div-info > svg {
  &:hover {
    cursor: pointer;
    filter: $svg-blue;
  }
  &:focus {
    transform: scale(1.1)
  }
}


.about-p {
  color: $lighter-grey;
}


.info-tech {
  font-size: .8em;
}




/**********     iPhone 5s     **********/

@media screen and (max-width: 1020px) {

  .Projects {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    width: 96%;
    margin-top: 2%;
    align-items: center;

  }

  .projects-grid {
    display: flex;
    flex-direction: column;

  }

  .projects-div-footer {
    height: 4vh;
  }

  .projects-div {
    width: 100%;
    margin-top: 2vh;
    margin-bottom: 2vh;
    flex-shrink: 0;
  }

  .gutenreader-div {
    align-items: center;
    display: flex;
    flex-direction: column;
    width: 80%;
  }

  .projects-div {
    opacity: 1;
  }

}

